@import "all-page";



#main {
  .container {
    position: relative;
    .text {
      font-family: $sus;
      position: absolute;
      top: vw(386);
      right: vw(200);
      font-size: vw(220);
      color: $brown;
      z-index: 5;
    }
    .color_wholesale {
      color: $brown;
      font-size: vw(32);
      text-transform: uppercase;
      margin-bottom: vw(17);
      display: flex;
      font-family: $cgm;
    }
    h1 {
      text-align: left;
      margin-bottom: vw(206);
      span {
        margin-left: vw(283);
      }
    }
    p, .mail {
      font-size: vw(32);
      line-height: vw(39);
      font-family: $cgm;
    }
    .mail {
      color: $brown;
      display: flex;
      margin-bottom: vw(68);
      text-transform: initial;
    }

    .instagram, .send_message, .pinterest {
      font-size: vw(24);
      line-height: vw(29);
      font-family: $cgb;
      color: $brown;
      display: inline-block;
      border-bottom: 1px solid $brown;
    }
    .instagram, {
      margin-right: vw(68);
    }
    .send_message {
      display: none;
    }
  }
}

@media screen and (max-width:  767px) {
  #main {
    padding-top: vw_m(73);
    height: 100vh;
    display: flex;
    align-items: center;
    .container {
      .text {
        position: relative;
        right: auto;
        top: auto;
        font-size: vw_m(60);
        line-height: vw_m(60);
        text-align: center;
        margin-top: vw_m(-20);
        margin-bottom: vw_m(40);
      }
      h1 {
        font-size: vw_m(48);
        text-align: center;
        color: $dark;
        margin-bottom: 0 !important;
        span {
          margin: 0;
        }
      }
      p {
        font-size: vw_m(16);
        line-height: vw_m(20);
        margin-bottom: vw_m(17);
        color: $dark;
        text-align: center;
      }
      .mail {
        display: none;
      }
      .send_message, .instagram, .pinterest {
        display: block;
        font-size: vw_m(24);
        line-height: vw_m(29);
        margin-right: auto;
        margin-left: auto;
        width: fit-content;
        margin-bottom: vw_m(10);
      }
    }
  }
}
