#header {
  z-index: 100;
  width: 100%;
  top: 0;
  left: 0;
  position: fixed;
  display: flex;
  align-items: center;
  box-shadow: 0 3px 10px rgba(32, 32, 32, .05);
  background: rgba(249, 245, 239, .9);
  //backdrop-filter: blur(5px);
  justify-content: center;
  flex-direction: column;
  .info {
    height: vw(40);
    width: 100%;
    display: flex;
    align-items: center;
    border-bottom: 1px solid #ccc;
    position: relative;
    .close {
      position: absolute;
      top: 0;
      bottom: 0;
      margin: auto;
      right: vw(34);
      cursor: pointer;
    }
    .container {
      display: flex;
      align-items: center;
      justify-content: space-between;
      p {
        color: $brown;
      }
    }
  }
  &>.container {
    display: flex;
    align-items: center;
    justify-content: space-between;
    height: vw(136);

    .logo {
      width: vw(140);
      img {
        width: 100%;
      }
    }
    .mob_basket, .link_mob_menu {
      display: none;
    }
    .nav {
      width: 50%;
      display: flex;
      &:nth-of-type(2) {
        justify-content: flex-end;
      }
      .menu {
        display: grid;
        grid-auto-flow: column;
        grid-column-gap: vw(68);

        li {
          display: flex;
          align-items: center;
          position: relative;
          a {
            display: flex;
            align-items: center;
            &:nth-of-type(2) {
              margin-left: vw(17);
            }
          }
          .checkout {

            img {
              margin-left: vw(5);
              width: vw(7);
            }
          }
          .quantity {
            font-family: $ifl;
            font-weight: bold;
            margin-left: vw(5);
          }
          ul {
            z-index: -1;
            position: absolute;
            top: 0;
            left: 50%;
            transform: translateX(-50%);
            transition: all 0.5s;
            opacity: 0;
            background: $bg;
            padding-top: vw(5);
            li {
              margin-bottom: vw(10);
            }
          }
          &:hover {
            ul {
              z-index: 10;
              opacity: 1;
              top: 100%;
            }
          }
        }
      }
    }
  }
  /*&.offset {
    height: vw(100);
    .logo {
      width: vw(120);
    }
  }*/
}

#search {
  position: fixed;
  width: 100%;
  height: 100%;
  z-index: -1;
  transition: z-index 0s .5s;
  &:before {
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: rgba(32, 32, 32, .3);
    opacity: 0;
    z-index: -2;
    transition: all 0.5s;
  }
  &:after {
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: vw(136);
    background: #fff;
    z-index: -1;
    transform: translateY(-100%);
    transition: all 0.5s;
  }
  .container {
    height: vw(136);
    display: flex;
    align-items: center;
    justify-content: center;
    transform: translateY(-100%);
    transition: all 0.5s;
    .input-with-button {
      width: vw(990);
    }
  }
  &.trans_mob {
    z-index: 10000;
    transition: z-index 0s;
    &:before {
      opacity: 1;
    }
    &:after, .container {
      transform: none;
    }
  }
  #my-search-48545077 {
    width: 100%;
    .ecwid-search-widget {
      max-width: 100% !important;
    }
    input {
      border: none !important;
      box-shadow: none !important;
      width: 100%;
    }
  }
}


@media screen and (max-width: $startMobile) {
  #header {
    height: vw_m(73);
    .container {
      .mob_basket, .link_mob_menu {
        font-size: vw_m(16);
        display: flex;
        align-items: center;
        color: $brown;
        font-family: $csb;
        transition: .5s all;
        &:hover {
          color: $dark;
        }
        .quantity {
          font-family: $ifl;
          font-weight: bold;
          font-size: vw_m(14);
          margin-left: vw_m(5);
        }
      }
      .nav {
        display: none;
      }
      .logo {
        width: vw_m(73);
        position: absolute;
        top: 0;
        left: 0;
        right: 0;
        bottom: 0;
        margin: auto;
        display: flex;
        align-items: center;
        justify-content: center;
      }
    }

  }
  #search {
    &:after {
      height: vw_m(73);
    }
    .container {
      height: vw_m(73);
      .input-with-button {
        width: 100%;
      }
    }
  }
}
