.mob_menu {
  display: none;
}

@media screen and (max-width: 767px) {
  .mob_menu {
    transition: .5s all;
    transform: translateX(-100%);
    width: 100%;
    z-index: 99999;
    height: 100%;
    left: 0;
    top: 0;
    position: fixed;
    display: block;
    background: rgb(52, 52, 46);
    .container {
      display: flex;
      flex-direction: column;
      justify-content: space-between;
      height: 100%;
      padding-bottom: vw_m(34);
      .top_menu_mob {
        display: flex;
        justify-content: space-between;
        align-items: center;
        position: relative;
        height: vw_m(80);
        .close, .search {
          border: none;
          background: transparent;
          line-height: vw_m(25);
          font-size: vw_m(16);
          font-family: $csb;
          text-transform: uppercase;
          color: #fff;
        }
        .logo_mob {
          width: vw_m(73);
          position: absolute;
          top: 0;
          left: 0;
          right: 0;
          bottom: 0;
          margin: auto;
          display: flex;
          align-items: center;
          justify-content: center;
          img {
            width: 100%;
          }
        }
      }
      .center_menu_mob {
        .nav {
          .menu {
            li {
              display: flex;
              justify-content: center;
              margin: vw_m(5) 0;
              a {
                font-size: vw_m(50);
                line-height: vw_m(55);
                color: #fff;
                transition: .5s all;
                font-family: "IvarFineLight";
                &.active {
                  margin-bottom: vw_m(10);
                }
                &:hover {
                  color: rgb(209, 157, 105);
                  border-bottom: 1px solid rgb(209, 157, 105);
                }
              }
            }
          }
        }
      }
      .bottom_menu_mob {
        .socials_mob {
          display: flex;
          flex-direction: column;
          align-items: center;
          a {
            font-size: vw_m(16);
            line-height: vw_m(20);
            margin-bottom: vw_m(10);
            color: #fff;
            border-bottom: 1px solid #fff;
            font-family: $csb;
            &:last-child {
              margin-bottom: 0;
            }
          }
        }
      }
    }
  }
  .trans_mob {
    transform: none;
  }
}
