.checkout_sidebar {
  bottom: 0;
  position: fixed;
  right: 0;
  transition: all .5s, z-index 0s .5s;
  left: 0;
  z-index: -1;
  top: 0;
  &:before {
    content: '';
    width: 100%;
    height: 100%;
    position: absolute;
    left: 0;
    top: 0;
    background: rgba(32, 32, 32, .3);
    transition: all 0.5s;
    opacity: 0;
  }
  .container {
    max-width: 100%;
    width: calc( 100% - 60px);
    margin: 0 auto;
  }
  .wrapper_checkout {
    transform: translateX(100%);
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    position: absolute;
    top: 0;
    width: vw(390);
    box-shadow: -10px 0px 30px rgba(32, 32, 32, .1);
    background: #fff;
    bottom: 0;
    right: 0;
    transition: all 0.5s;

    .top_checkout {
      border-bottom: 1px solid rgb(204, 204, 204);
      display: flex;
      align-items: center;
      height: vw(136);
      .container {
        display: flex;
        align-items: center;
        justify-content: space-between;
        .close_checkout, .search {
          font-size: vw(16);
          cursor: pointer;
          color: $dark;
          border: none;
          text-transform: uppercase;
          background: transparent;
          line-height: vw(20);
          font-family: $csb;
        }
        .logo_mob {
          display: none;

        }
        .search {
          .quantity {
            font-family: $ifl;
            font-weight: bold;
            font-size: vw_m(14);
            margin-left: vw_m(5);
          }
        }
      }
    }
    .content_checkout {
      flex: 1;
      .item {
        padding-top: vw(34);
        display: flex !important;
        &:last-child .container {
          border-bottom: none;
        }
        .container {
          display: flex;
          border-bottom: 1px solid rgb(204, 204, 204);
          padding-bottom: vw(34);
          align-items: center;
          justify-content: space-between;
          img {
            width: vw(57);
          }
          .description_product {
            h4 {
              text-transform: initial;
              margin-bottom: vw(4);
            }
            p {
              font-size: vw(14);
              line-height: vw(17);
              font-family: $csb;
              span {
                font-family: $ifl;
              }
            }
          }
          .calculation {
            height: 100%;
            display: flex;
            flex-direction: column;
            align-items: flex-end;
            justify-content: space-between;
            .remove {
              font-size: vw(14);
              line-height: vw(20);
              font-family: $csb;
              text-transform: uppercase;
              color: $brown;
              &:before {
                background: $brown;
              }
            }
            .price {
              font-size: vw(24);
              line-height: vw(39);
              color: rgb(31, 31, 31);
              font-family: $ifl;
            }
            .counter {
              display: flex;
              align-items: center;
              justify-content: space-between;
              border: 1px solid rgb(32, 32, 32);
              .decrement, .increment, .count {
                font-size: vw(15);
                background: #fff;
                line-height: vw(21);
                display: flex;
                align-items: center;
                justify-content: center;
                font-family: $ifl;
                cursor: pointer;
                border: none;
                width: vw(25);
                height: vw(25);
              }
              .count {
                border-right: 1px solid rgb(32, 32, 32);
                border-left: 1px solid rgb(32, 32, 32);
                box-sizing: content-box;
              }
              .decrement, .increment {
                font-weight: 700;
              }
            }
          }
        }
      }
    }
    .bottom_checkout {
      border-top: 1px solid rgb(204, 204, 204);
      .container {
        padding: vw(34) 0;
        display: flex;
        flex-direction: column;
        align-items: flex-start;
        .popup_add_note {
          color: $brown;
          text-transform: initial;
          font-size: vw(16);
          line-height: vw(22);
          font-family: $csb;
          margin-bottom: vw(8);
          display: block;
          text-transform: uppercase;
        }
        p {
          margin-bottom: vw(17);
          font-size: vw(16);
          line-height: vw(22);
          font-family:  $csb;
        }
        .button_checkout {
          display: flex;
          width: 100%;
          padding: vw(16) vw(40);
          cursor: pointer;
          justify-content: space-between;
          align-items: center;
          border: none;
          background: rgb(32, 32, 32);
          span {
            font-size: vw(22);
            line-height: vw(31);
            font-family: $ifl;
            color: #fff;
          }
        }
      }
    }
  }
}
@media screen and (min-width: 768px) and (max-width: 1023px) {
  .checkout_sidebar {
    .wrapper_checkout {
      width: vw(500);
    }
  }
}

@media screen and (max-width: 767px) {
  .checkout_sidebar {
    background: transparent;
    .wrapper_checkout {
      width: 100%;
      .top_checkout {
        border-bottom: none;
        height: vw_m(73) !important;
        box-shadow: 0 3px 10px rgba(32, 32, 32, .05);
        .container {
          .logo_mob {
            display: block;
            img {
              width: vw_m(74) !important;
            }

          }
          .close_checkout, .search {
            font-size: vw_m(16) !important;
            line-height: vw_m(20);
            display: flex;
            align-items: center;
          }
        }
      }
      .content_checkout {
        overflow-y: scroll;
        .item {
          padding-top: 0;
        }
        .container {
          padding: vw_m(34) 0 !important;
          img {
            width: vw_m(57) !important;
          }
          .description_product {
            p {
              font-size: vw_m(14) !important;
              line-height: inherit !important;
              color: rgb(32, 32, 32);
            }
          }
          .calculation {
            .remove {
              font-size: vw_m(14) !important;
              line-height: vw_m(20) !important;
            }
            .price {
              font-size: vw_m(24) !important;
              line-height: vw_m(39) !important;
            }
            .counter {
              .decrement, .increment, .count {
                font-size: vw_m(15) !important;
                line-height: vw_m(21) !important;
                width: vw_m(25) !important;
                height: vw_m(25) !important;
              }
            }
          }
        }
      }
      .bottom_checkout {
        .container {
          p {
            margin-bottom: vw_m(16);
          }
        }
      }
      .button_checkout {
        padding: vw_m(16) vw_m(40) !important;
        span {
          font-size: vw_m(22) !important;
          line-height: vw_m(31) !important;
        }
      }
      .popup_add_note {
        font-size: vw_m(14) !important;
        line-height: vw_m(22) !important;
      }
      p {
        font-size: vw_m(16) !important;
        line-height: vw_m(22) !important;
      }
    }
  }
}

.trans_mob {
  opacity: 1;
  z-index: 9999;
  transition:  all .5s, z-index 0s;
  &:before {
    opacity: 1;
  }
  .wrapper_checkout {
    transform: translateX(0) !important;
  }
}
