@import "variable";
@import "header";
@import "footer";
@import "mob-menu";
@import "checkout";

::-webkit-scrollbar {
  width: 0;
}

::-webkit-scrollbar-track {
  background: #f1f1f1;
}

::-webkit-scrollbar-thumb {
  background: $brown;
}

::-webkit-scrollbar-thumb:window-inactive {
  background: $brown;
}

::selection {
  background: #34342E;
  color: #fff;
}

input:focus::-webkit-input-placeholder { /* Chrome/Opera/Safari */
  color: transparent;
}

input:focus::-moz-placeholder, input:focus:-ms-input-placeholder, input:focus:-moz-placeholder, textarea:focus::-webkit-input-placeholder { /* Firefox 19+ */
  color: transparent;
}

input::-webkit-input-placeholder {  /* Chrome/Opera/Safari */
  color: $dark !important;
}

input:-moz-placeholder {
  color: $dark !important;
}

input:-ms-input-placeholder {
  color: $dark !important;
}

input:-moz-placeholder {
  color: $dark !important;
}

textarea::-webkit-input-placeholder {
  color: $dark !important;
}

input[type="number"]::-webkit-input-placeholder {
  color: $dark;
}

a, a:focus, button, button:focus, input, input:focus, textarea, textarea:focus {
  outline: 0 !important;
}

:focus {
  outline: 0 !important;
  outline-offset: 0 !important;
}

select, select:focus {
  outline: 0 !important;
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

html {
  position: relative;
}

body {
  background: $bg;
  position: relative;
  color: $dark;
}

.hidden {
  display: none !important;
}

#my-store-48545077 {

  .ec-notices.ec-notices--animation-default.ec-notices--order-direct.ec-notices--fixed.ec-notices--top.ec-notices--right {
    top: auto !important;
    bottom: 32px !important;
  }

  h1 {
    text-align: left;
    font-weight: normal !important;
    font-size: 32px !important;
    color: $dark !important;
    margin-bottom: 0 !important;
  }
  /*.grid-product.grid-product--has-additional-image {
    width: 25% !important;
    box-sizing: border-box !important;
  }*/
  .product-details__product-description {
    div, p {
      font-family: $csb;
      font-size: 16px;
      line-height: 22px;
      text-shadow: 0 0 1px rgba(0,0,0,.3);
      letter-spacing: .04em !important;
    }
  }
  .ec-link {
    color: $brown !important;
  }
  .page-title__name.ec-header-h4 {
    text-align: left;
    font-weight: normal !important;
    text-shadow: 0 0 1px rgba(0,0,0,.3);
    text-transform: uppercase;
    font-family: $csb;
  }
  .product-details__sidebar {
    display: flex;
    flex-direction: column;
  }
  .ec-breadcrumbs {
    display: flex !important;
    align-items: center;
    order: -1;
  }
  .product-details__description.product-details__description--blank {
    display: none;
  }
  .breadcrumbs__link {
    text-shadow: 0 0 1px rgba(0,0,0,.3);
    letter-spacing: .04em !important;
    text-transform: uppercase !important;
    color: $brown !important;
    position: relative !important;
    line-height: 20px !important;
    display: block;
    width: max-content;
    &:before {
      content: '';
      top: 100%;
      left: 0;
      position: absolute;
      width: 0;
      height: 1px;
      background: $brown;
      transition: width 0.5s;
    }
    &:hover {
      &:before {
        width: 100%;
        right: 0;
        left: auto;
      }
    }
  }
  .product-details-module__content {
    font-family: $csb;
  }
  .details-product-price__value.ec-price-item.notranslate {
    font-family: $csb;
    font-size: 32px;
  }
  .product-details-module {
    margin-bottom: 20px !important;
  }
  .form-control__text {
    border: none !important;
  }
  .form-control:not(.details-product-purchase__add-to-bag) .form-control__button {
    span {
      font-family: $csb;
      font-weight: normal;
      font-size: 22px;
      letter-spacing: .04em !important;
    }
  }
  /*.product-details-module__title {
    font-family: $ifl;
    text-transform: uppercase;
    letter-spacing: .08em !important;
    font-weight: 900 !important;
    span {
      letter-spacing: .08em !important;
    }
  }*/
  .details-product-purchase__checkout .form-control__button {
    background: $dark !important;
  }
  .details-product-purchase__add-to-bag .form-control__button {
    background: none !important;
    font-weight: normal !important;
    width: max-content !important;
    box-shadow: none !important;
    border: none !important;
    padding: 0 !important;
    &:before {
      content: '';
      position: absolute;
      top: 100%;
      left: 0;
      width: 100%;
      background: $brown;
      height: 1px;
      transition: width .4s .4s;
    }
    span {
      font-size: vw(32);
      line-height: vw(44);
      font-family: $ifl;
      color: $brown;
      padding-bottom: vw(10);
      position: relative;
      transition: all .4s;
      text-transform: uppercase;
    }
    &:after {
      content: 'Aethetic';
      font-family: $sus;
      text-transform: none;
      position: absolute;
      top: 45%;
      left: 0;
      right: 0;
      margin: auto;
      text-align: center;
      font-size: vw(40);
      opacity: 0;
      color: $brown;
      transition: all .4s;
    }
    &:hover {
      span {
        color: $dark;
      }
      &:before {
        width: 0;
        transition-delay: 0s;
        right: 0;
        left: auto;
      }
      &:after {
        transition-delay: .4s;
        opacity: 1;
      }
    }
  }
}

a:focus, a:hover {
  text-decoration: none;
}

ul {
  list-style: none;
  margin: 0;
  padding: 0;
}

p {
  font-size: vw(16);
  white-space: pre-line;
  line-height: 115%;
  color: $dark;
  font-family: $csb;
}

a {
  text-decoration: none;
  color: $dark;
  text-transform: uppercase;
  font-family: $csb;
  font-size: vw(16);
  cursor: pointer;
}

.link {
  position: relative;
  color: $dark;
  transition: all 0.5s;
  &:before {
    content: '';
    position: absolute;
    top: 100%;
    height: 1px;
    width: 0;
    left: 0;
    right: 0;
    margin: auto;
    background: $brown;
    transition: width 0.5s;
  }
  &:hover, &.active {
    color: $brown !important;
    &:before {
      width: 100%;
    }
  }
  &.line {
    &:before {
      background: $dark;
      width: 100%;
      right: auto;
      transition: width 1.25s cubic-bezier(1, 0, 0, 1);
    }
    &:hover {
      color: $dark;
      &:before {
        width: 0;
        left: auto;
        right: 0;
      }
    }
  }
}

.input-with-button {
  position: relative;
  display: flex;
  align-items: center;
  border-bottom: 1px solid $brown;
  padding: 0 vw(14);
  height: vw(48);

  input {
    border: none;
    background: transparent;
    width: 100%;
    font-size: vw(16);
    color: $dark;
    font-family: $cgb;
  }

  .submit {
    font-family: $cgb;
    border: none;
    font-size: vw(14);
    background: transparent;
    cursor: pointer;
    display: flex;
    align-items: center;

    img {
      width: vw(5);
      margin-left: vw(20);
    }
  }
}

$animationDuration: .4s;
.linkWithText {
  font-size: vw(32);
  line-height: vw(44);
  font-family: $ifl;
  color: $brown;
  padding-bottom: vw(10);
  position: relative;
  transition: all $animationDuration;
  &:before {
    content: '';
    position: absolute;
    top: 100%;
    left: 0;
    width: 100%;
    background: $brown;
    height: 1px;
    transition: width $animationDuration $animationDuration;
  }
  span {
    font-family: $sus;
    text-transform: none;
    position: absolute;
    top: 45%;
    left: 0;
    right: 0;
    margin: auto;
    text-align: center;
    font-size: vw(40);
    opacity: 0;
    color: $brown;
    transition: all $animationDuration;
  }
  &:hover {
    color: $dark;
    &:before {
      width: 0;
      transition-delay: 0s;
      right: 0;
      left: auto;
    }
    span {
      transition-delay: $animationDuration;
      opacity: 1;
    }
  }
}


.fade-enter-active, .fade-leave-active {
  transition: opacity .5s;
}

.fade-enter, .fade-leave-to /* .fade-leave-active до версии 2.1.8 */
{
  opacity: 0;
  transition-delay: .5s;
}

.wow {
  visibility: hidden;
}

