.advantages {
  border-top: 1px solid #ccc;
  border-bottom: 1px solid #ccc;
  padding: vw(100) 0;
  display: none;
  .container {
    display: flex;
    justify-content: space-around;
    align-items: center;
    .item {
      display: flex;
      flex-direction: column;
      align-items: center;
      img {
        height: vw(43);
      }
      .line {
        background: $brown;
        height: 1px;
        width: vw(68);
        margin: vw(18) 0;
      }
      h3 {
        margin-bottom: vw(17);
      }
      p {
        text-align: center;
        font-family: $cgm;
        span {
          font-family: $ifl;
        }
      }
    }
  }
}
#footer {
  margin-top: vw(100);
  .advantages {
    display: block;
  }
  .top_footer {
    padding: vw(68) 0;
    border-bottom: 1px solid #ccc;

    .container {
      display: flex;
      justify-content: space-between;

      .nav {
        .menu {
          li {
            font-family: 'CormorantGaramondBold';
            &:not(:last-of-type) {
                margin-bottom: vw(15);
            }
            a {
              font-size: vw(14);

              &:last-child {
                margin-bottom: 0;
              }
            }
          }
        }


      }
      .form {
        width: vw(600);
        display: flex;
        flex-direction: column;
        justify-content: space-between;

        h4 {
          text-align: left;
        }

        .text_form {
          color: #323232;
          margin-top: vw(10);
          font-family: $csb;
        }

        .input-with-button {
          margin-top: auto;
        }
      }
    }


  }
  .bottom_footer {
    padding: vw(70);

    .container {
      display: flex;
      justify-content: space-between;
      align-items: center;
      position: relative;
      z-index: 1;

      .socials {
        .inst {
          margin-right: vw(68);
        }
        a {
          color: $dark !important;
        }
      }

      p {
        color: #323232;

        a {
          margin-right: vw(68);
        }
      }

      .copyright {
        font-size: vw(16);
        font-family: $csb;
        letter-spacing: .02em;
        position: absolute;
        top: 0;
        bottom: 0;
        left: 0;
        right: 0;
        margin: auto;
        display: flex;
        align-items: center;
        justify-content: center;
        z-index: -1;
      }

      .cards {
        img {
          height: vw(35);
        }
        .card {
          margin-right: vw(18);

          img {
            width: vw(33);
          }

          &:last-child {
            margin-right: 0;
          }
        }
      }
    }
  }
}
@media screen and (max-width: $startMobile) {
  .advantages {
    display: block;
    .container {
      width: 100%;
      .slick-track {
        display: flex;
        align-items: center;
      }
      .item {
        width: 100%;
        img {
          height: vw_m(30);
        }
        .line {
          width: vw_m(50);
          margin: vw_m(10) 0;
        }
        p {
          width: 70%;
        }
      }
    }
  }
  #footer {
    .top_footer {
      padding: vw_m(34) 0;

      .container {
        flex-direction: column;

        .form {
          margin-bottom: vw_m(34);
          width: 100%;
          order: -1;

          .text_form {
            font-size: vw_m(16);
            font-family: $csb;
            text-align: center;
            padding: 0 vw_m(30);
            margin-bottom: vw_m(34);
          }
        }

        .nav {

          .menu {
            display: flex;
            flex-direction: column;

            li {
              text-align: center;
              margin-bottom: vw_m(10);

              &:last-child {
                margin-bottom: vw_m(10);
              }

              a {
                font-size: vw_m(14);
              }

            }

            #footer_link_one {
              order: 2;
            }

            #footer_link_two {
              order: 1;
            }

            #footer_link_three {
              order: 0;
            }

            #footer_link_four {
              order: 3;
            }

            #footer_link_five {
              order: 2;
            }

            #footer_link_six {
              order: 1;
            }

            #footer_link_seven {
              order: 0;
            }

            #footer_link_eight {
              order: 3;
            }
          }
        }
      }
    }

    .bottom_footer {
      padding: vw_m(34) 0 vw_m(68);

      .container {
        width: calc(100% - 100px);
        flex-direction: column;

        .socials {
          display: flex;
          justify-content: space-between;
          width: 100%;
          margin-bottom: vw_m(34);
          order: 0;

          a {
            font-size: vw_m(14);
            line-height: vw_m(16);
            display: flex;
          }
        }

        .cards {
          width: 100%;
          display: flex;
          margin-bottom: vw_m(34);
          justify-content: space-between;
          order: 1;
          img {
            width: 100%;
            height: auto;
          }

          .card {
            img {
              width: vw_m(40);
            }
          }
        }

        .copyright {
          width: 100%;
          order: 2;
          font-size: vw_m(14);
          text-align: center;
          position: static;
        }
      }
    }
  }
}
